body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  padding: 5px 10px;
  font-size: 20px;
  text-align: center;
  display: block;
  font-size: 20px;
  width: 80%;
  margin: 0 auto;
  margin-bottom: 15px;
}
input:focus {
  background-color: rgb(80, 80, 80);
  border-color: white;
  color: white;
}

.form button {
  padding: 3px 10px;
  text-transform: uppercase;
  display: block;
  margin: auto;
  font-size: 20px;
  border: 2px solid black;
  background-color: white;
  cursor: pointer;
  transition: 0.3s;
}
.form button:hover {
  background-color: rgb(80, 80, 80);
  border-color: white;
  color: white;
}
.finishDateLabel {
  text-align: center;
  margin-bottom: 10px;
  font-size: 20px;
}
.importantLabel {
  text-align: center;
  margin-bottom: 10px;
  font-size: 20px;
}

.App {
  text-align: center;
  padding: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: "Dancing Script", cursive;
  font-size: 40px;
  border: 2px solid white;
  background-color: rgb(44, 44, 44);
}
body {
  background-color: rgb(31, 31, 31);
  color: white;
  text-align: left;
  margin-left: 30px;
  margin-right: 30px;
}
h1,
h2,
h3 {
  text-align: center;
}
.taskStyle {
  padding: 5px;
  border-bottom: 1px dotted gray;
}
.taskButton {
  text-align: right;
}
.deleteButton {
  text-align: right;
}
.taskButton button {
  width: 200px;
  font-size: 15px;
  border: 2px solid black;
  background-color: white;
  cursor: pointer;
  transition: 0.3s;
}
.deleteButton button {
  width: 200px;
  margin-top: 10px;
  font-size: 15px;
  border: 2px solid black;
  background-color: white;
  cursor: pointer;
  transition: 0.3s;
}
.taskButton button:hover,
.deleteButton button:hover {
  background-color: rgb(80, 80, 80);
  border-color: white;
  color: white;
}

